ort and facilitate the attainment of the CBN’s monetary policy objective of stable exchange rate.  \n            ")])])])])]),_c('section',{attrs:{"id":"newss"}},[_c('div',{staticClass:"container pb-5"},[_c('p',{staticClass:"p-16 pt-5"},[_vm._v("\n        Naijabdcs.com   provides authentic and live foreign exchange (buying and selling) rates across the country based on market realities.\n        The forex rates published by naijabdcs.com are sourced real-time through \n        ABCON’s network of over 4000 CBN licensed BDCs operating in various locations across the six geopolitical zones of the country. \n        Association of Bureaux De Change Operators of Nigeria (ABCON) is the umbrella body of all BDCs licensed by the CBN. \n        The historical background of ABCON dates back to 4th of February, 1997 when its registration was approved by the Corporate Affairs Commission (C.A.C). It came into being from a vision of some bureaux de change operators numbering more than \n        50(fifty) in an attempt to create an environment of sanctity in the business of foreign exchange. \n        Hither-to, the country-Nigeria was experiencing an economic recession which emanated from the restrictions placed on foreign exchange by a government policy named \"Structural Adjustment Program\" (SAP) prevalent in the '80s.\n\n        The government of the day in an attempt to ration the scarce foreign exchange in circulation introduced measures to restrict access to it through an outright \n        ban on some transactions in the official foreign exchange market and brought in rigorous documentation.\n\n       ")]),_c('p',{staticClass:"p-16 pb-5"},[_vm._v("\n         This created a huge demand for foreign exchange outside government approved sources. It also marked the beginning of the black market or the parallel market. \n         Moreover, the tighter restrictions on the activities at the parallel market gave rise to an increasing problem of naira instability which by implication resulted to economic \n         inflation. Furthermore, in an attempt to stall the unfavourable trend of naira instability and inflation, the Central Bank of Nigeria in 2006 brought in operators of bureaux \n         de change in the country into the official market, with the objective of encouraging them to buy at official exchange rate and sell to end-users within an approved margin. \n         From a humble beginning of about 50 members in 1989 under the pioneering head, then called the Chairman of the committee, Chief Paul Ehiri and other pioneering \n         members that included Late Dr.John Anaza, Mr. Moses B. Adeisa, Mr. James O. Tifase and Alhaji Suleiman Farouk, the association has grown from strength to strength with \n         a membership numbering above 3000 as at date.With this numerical strength and spread, the association is poised to fulfil its objective of working closely with CBN to \n         meet the public’s yearning for a legitimate business in foreign exchange.\n       ")])])]),_c('section',{attrs:{"id":"new"}},[_c('div',{staticClass:"container"},[_c('div',{staticClass:"row"},[_c('div',{staticClass:"col-lg-6 col-xs-12"},[_c('p',{staticClass:"b-left p-16 py-5 px-3"},[_vm._v("\n              Sequel to this, the Country's apex bank introduced Cash sale of dollars to Bureaux  \n              De Change Operators. This new method was encapsulated in the \"FURTHER   \n              MEASURES TO LIBERALISE THE FOREIGN EXCHANGE MARKET IN NIGERIA”,   \n              introduced in March 2006 and which came in to effect from April same year.    \n              Moreover, with the official empowerment of Bureaux De Change Operators in the   \n              country to see to the sale and purchase of foreign exchange, the stage was set for    \n              operators in the financial sector to operate more effectively. This has been a major    \n              boost to the relevance of ABCON in Nigeria’s financial sector.\n            ")])]),_c('div',{st